var that;
export default {
	data() {
		return {
			urlyes: 'https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/images/2020/03/31/0d6d6d3cbf0bcfc82db82d78dc1193cf.png',
			url: '',
			name: '',
			stridcard: ''

		}
	},
	onLoad: function(e) {
		that = this;
		that.url = e.imgurl
	},
	onShow: function(res) {

	},
	methods: {
		nextto: function() {
			console.log(that.name);
			console.log(that.stridcard);
			if (that.name == '') {
				uni.showToast({
					title: '请填写姓名'
				})
				return
			}
			if (that.stridcard == '') {
				uni.showToast({
					title: '请填写身份证号'
				})
				return
			}

			uni.request({
				url: this.$api_url + '/user/doVerify', //仅为示例，并非真实接口地址。
				method: 'POST',
				header: {
					// 'Content-Type': 'application/x-www-form-urlencoded',
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				data: {
					 "usergroup": 1,
					"user_verified_category_id": 1,
					"realname": that.name,
					"idcard": that.stridcard,
					"expertIntro": "这方面我可是专家，值得您的信赖"
				},
				success: res => {
					console.log(res)
					if (res.data.code == 1000) {
						uni.showToast({
							title: "提交成功"
						});

						setTimeout(function() {
							uni.navigateBack({
								delta: 2
							});
						}, 1000);
					}
				}
			});

		}
	}

}
